@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,600;0,700;0,800;1,400&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Open Sans", arial;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

:root {
  --bar: 0%;
  --blue: #20a1d4;
  --green: #1cbc2c;
  --orange: #ef5f3c;
  --black: #424242;
  --grey: #777;
  --light-grey: #eaeaea;
}

p {
  margin: 1em 3em;
}

strong {
  font-weight: 800;
}

.header-container {
  background-color: #424242;
  color: #eaeaea;
  width: 24em;
  height: 4em;
  border-radius: 5px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.header-container:after {
  top: 100%;
  left: 87%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(66, 66, 66, 0);
  border-top-color: #424242;
  border-width: 10px;
  margin-left: -10px;
}

.orange-bar {
  background-color: #f9fafb;
  width: 24em;
  height: 1em;
  border: 0.5px var(--light-grey) solid;
  margin: auto;
}

.bar {
  width: var(--bar);
  background-color: var(--orange);
  height: 1em;
}

.App {
  display: flex;
  flex-direction: column;
}

.main-container {
  display: flex;
  flex-direction: column;
  width: 24em;
  margin: 15px auto;
  border: 0.3px var(--light-grey) solid;
  border-radius: 5px;
  padding-bottom: 2em;
  color: var(--grey);
  font-weight: 600;
}

input[type="number"] {
  text-align: center;
}

.form {
  display: flex;
  justify-content: center;
}

.form-item {
  width: 8em;
  height: 3em;
  margin: 0 10px;
  padding: 0;
  border: 0.5px var(--light-grey) solid;
  border-radius: 5px;
  font: 700 17px "Open Sans";
}

.donate-button {
  background-color: var(--green);
  color: white;
  border: unset;
}

span {
  color: var(--orange);
  font-weight: 700;
}

em {
  color: var(--blue);
  margin: 1em 0 0 3em;
  font-weight: 600;
}

.button-container {
  margin: auto;
  display: flex;
  justify-content: center;
  width: 24em;
}

.grey-buttons {
  margin: 0 20px 0 0;
  background-color: #f9fafb;
  color: var(--grey);
  border: 0.5px var(--light-grey) solid;
  border-radius: 5px;
  height: 3em;
  width: 100%;
  font-weight: 700;
  font-size: 17px;
}

